@use '../utils/' as *;

.story{
    &__pictures{
        background-color: var(--color-primary);
        grid-column: full-start / col-end 4;
        display: grid;
        grid-template-rows:repeat(6,1fr);
        grid-template-columns:repeat(6,1fr);
        align-items:center;
        background-image: url(/img/back.jpg);
    }

    &__img--1{
           width: 100%; // 100 about the container because if we move, the picture take de responsive
           grid-row: 2/6;
           grid-column: 2/6;
           box-shadow: 0 2rem 5rem rgb(#0000,.1);
          
    }
    &__img--2{
        width: 115%;
        grid-row: 4/6;
        grid-column: 4/7;
        z-index: 20;
        box-shadow: 0 2rem 5rem rgb(#0000, .2);
    }

    &__content{
       
        grid-column: col-start 5 / full-end;
        padding: 6rem 10vh;
        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        // align-items: flex-start;

        display: grid;
        align-content: center;
        justify-items: start;

    }
    &__text{
        font-size: 1.5rem;
        font-style: italic;
    }
}