@use '../utils/' as *;

.homes{
    
    grid-column: center-start / center-end;
    display: grid;
    margin: 15rem 0;
    grid-template-columns: repeat(auto-fit,minmax(25rem,1fr));
    grid-gap: 7rem;
}

.home{
    background-color: var(--color-grey-light-1);
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-row-gap: 3.5rem;

    &__img{
        width: 100%;  
        grid-row: 1/2;
        grid-column: 1/-1;
        z-index: 1;

    }
    &__like{
        grid-row: 1/2;
        grid-column: 2/3;
        fill: var(--color-primary);
         height: 2.5rem;
         width: 2.5rem;
         z-index: 2;
         justify-self: flex-end;
         margin: 1rem;
    }

    &__name{
        grid-row: 1/2;
        grid-column: 1/-1;
        align-self: end;
        justify-self: center;
        z-index: 3;
        width: 80%;
        font-family: $font-display;
        font-size: 1.6rem;
        text-align: center; 
        padding: 1.25rem;
        background-color: var(--color-secondary);
        font-weight: 400;
        color: white;
        transform: translateY(50%);
    }
    &__location,
    &__rooms,
    &__area,
    &__price{
        font-size: 1.5rem;
        margin-left:2rem ;
        display: flex;
        align-items: center;
        svg{
            fill: var(--color-primary);
            height: 2rem;
            width: 2rem;
            margin-right: 1rem;
        }
    }

    &__btn{
        grid-column: 1/-1;
    }
}